@use 'variables' as v;
@use 'mixins' as m;

// Header
header {
    @include m.display-flex;
    height: 5rem;
    padding: 2rem 1rem;
    position: sticky;
    top: 0;
    background-color: v.$soft-blush-pink;
    z-index: 1000;
    box-shadow: 0 -6px 10px 5px rgba(0, 0, 0, 0.5);

    nav {
        ul {
            @include m.display-flex($justify: space-between);
            list-style: none;

            li {
                margin-left: 2rem;

                a {
                    color: v.$highlight;
                    text-decoration: none;
                    font-size: 1.5rem;
                    position: relative;
                    transition: all 0.3s ease;

                    &:hover,
                    &:focus {
                        text-shadow: 0 0 8px v.$terracotta;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        left: 0;
                        bottom: -2px;
                        @include m.height-width(2px, 0);
                        background-color: v.$highlight;
                        transition: width 0.3s ease;
                    }

                    &:hover::after,
                    &:focus::after {
                        width: 100%;
                    }
                }
            }
        }

        @include m.respond('tablet') {
            @include m.height-width(100%, 100%);
            display: none;
            position: fixed;
            z-index: 1;
            top: 0;
            left: 0;
            background-color: v.$soft-blush-pink;
            overflow-x: hidden;
            transition: 0.5s;
            padding: 2rem;
            font-size: 4rem;

            ul {
                display: block;
                margin-top: 8rem;
            }
        }
    }
}

.mobile_menu {
    display: none;
    position: absolute;
    right: 1rem;
    @include m.height-width(4rem, 4rem);
    cursor: pointer;

    @include m.respond('tablet') {
        display: block;
    }
}

.close_menu {
    display: none;
    @include m.height-width(4rem, 4rem);
    cursor: pointer;

    @include m.respond('tablet') {
        display: block;
        position: absolute;
        top: 1rem;
        right: 1rem;
    }
}