* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Space Mono", monospace;
  color: #1C1C1C;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Kode Mono", monospace;
}

p {
  font-size: 20px;
}

a {
  font-family: "Major Mono Display", monospace;
  text-decoration: none;
  font-weight: bold;
}

span {
  font-family: "Syne Mono", monospace;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}
span:hover, span:focus {
  text-decoration: underline;
}

section {
  padding: 4rem;
}
@media (max-width: 768px) {
  section {
    padding: 3rem;
  }
}
@media (max-width: 480px) {
  section {
    padding: 1rem;
  }
}

h2 {
  font-size: 3rem;
  color: #1e3a8a;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 1rem;
  flex-wrap: no-wrap;
}
@media (max-width: 480px) {
  h2 {
    font-size: 2rem;
  }
}
h2 span {
  font-family: inherit;
  border-bottom: 2px solid #86A8E7;
  cursor: default;
}
h2 span:hover, h2 span:focus {
  text-decoration: none;
}

h3 {
  font-size: 2rem;
}
@media (max-width: 480px) {
  h3 {
    font-size: 1.5rem;
  }
}

h4 {
  font-size: 1.5rem;
}
@media (max-width: 480px) {
  h4 {
    font-size: 1.2rem;
  }
}

.years {
  font-size: 1.5rem;
  font-weight: 400;
}

.header-icon {
  display: inline-block;
  height: 3rem;
  width: 3rem;
  border-bottom: none;
}

.tech-stack-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: wrap;
}
.tech-stack-container div {
  height: 2rem;
  width: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: no-wrap;
  position: relative;
  top: 0;
  cursor: pointer;
  margin: 0.5rem 1rem;
  font-family: "Cutive Mono", monospace;
}
.tech-stack-container div:hover, .tech-stack-container div:focus {
  top: -0.5rem;
}
.tech-stack-container div:hover span, .tech-stack-container div:focus span {
  visibility: visible;
  text-decoration: none;
}
.tech-stack-container div span {
  visibility: hidden;
  background-color: #D16A50;
  color: #FFFFFF;
  font-family: "Cutive Mono", monospace;
  text-align: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  height: auto;
  width: max-content;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: no-wrap;
  height: 5rem;
  padding: 2rem 1rem;
  position: sticky;
  top: 0;
  background-color: #f5dade;
  z-index: 1000;
  box-shadow: 0 -6px 10px 5px rgba(0, 0, 0, 0.5);
}
header nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 0;
  flex-wrap: no-wrap;
  list-style: none;
}
header nav ul li {
  margin-left: 2rem;
}
header nav ul li a {
  color: #e11d48;
  text-decoration: none;
  font-size: 1.5rem;
  position: relative;
  transition: all 0.3s ease;
}
header nav ul li a:hover, header nav ul li a:focus {
  text-shadow: 0 0 8px #D16A50;
}
header nav ul li a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  width: 0;
  background-color: #e11d48;
  transition: width 0.3s ease;
}
header nav ul li a:hover::after, header nav ul li a:focus::after {
  width: 100%;
}
@media (max-width: 768px) {
  header nav {
    height: 100%;
    width: 100%;
    display: none;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #f5dade;
    overflow-x: hidden;
    transition: 0.5s;
    padding: 2rem;
    font-size: 4rem;
  }
  header nav ul {
    display: block;
    margin-top: 8rem;
  }
}

.mobile_menu {
  display: none;
  position: absolute;
  right: 1rem;
  height: 4rem;
  width: 4rem;
  cursor: pointer;
}
@media (max-width: 768px) {
  .mobile_menu {
    display: block;
  }
}

.close_menu {
  display: none;
  height: 4rem;
  width: 4rem;
  cursor: pointer;
}
@media (max-width: 768px) {
  .close_menu {
    display: block;
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
}

.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 2rem;
  flex-wrap: no-wrap;
}
.hero h1 {
  font-size: 4rem;
  color: #1e3a8a;
}
@media (max-width: 768px) {
  .hero h1 {
    font-size: 3.5rem;
  }
}
@media (max-width: 480px) {
  .hero h1 {
    font-size: 2rem;
  }
}
.hero p {
  margin: 1rem 0;
  color: #D16A50;
  font-family: "Syne Mono", monospace;
  font-weight: 500;
}
.hero span {
  text-decoration: none;
  font-family: inherit;
}
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .hero-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0;
    flex-wrap: no-wrap;
  }
}

.hero-button {
  margin-top: 2rem;
}

.resume-btn {
  background-color: #e11d48;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  transition: all 0.5s ease;
  text-wrap: nowrap;
  height: auto;
  width: max-content;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0.5rem;
  flex-wrap: no-wrap;
}
.resume-btn:hover, .resume-btn:focus {
  background-color: #f5dade;
  color: #e11d48;
}
@media (max-width: 768px) {
  .resume-btn {
    font-size: 1.2rem;
    padding: 1rem 1.5rem;
  }
}
@media (max-width: 480px) {
  .resume-btn {
    font-size: 1rem;
    padding: 1rem;
  }
}
.resume-btn:hover, .resume-btn:focus {
  background-color: #f5dade;
  color: #e11d48;
}
.resume-btn path {
  fill: #FFFFFF;
}
.resume-btn:hover path, .resume-btn:focus path {
  fill: #e11d48;
}

.hero-text {
  font-size: 2.5rem;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  border-right: 2px solid #D16A50;
  animation: typing 10s steps(30) infinite, blink-caret 0.5s step-end infinite;
}
@media (prefers-reduced-motion: reduce) {
  .hero-text {
    animation: none;
  }
}
@media (max-width: 768px) {
  .hero-text {
    font-size: 1.5rem;
  }
}
@media (max-width: 480px) {
  .hero-text {
    font-size: 1.2rem;
  }
}
@keyframes typing {
  from, to {
    max-width: 0;
  }
  50% {
    max-width: 100%;
  }
}
@keyframes blink-caret {
  50% {
    border-color: transparent;
  }
}

.hero-image {
  height: 30rem;
  width: auto;
  margin-left: 2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
@media (max-width: 768px) {
  .hero-image {
    height: 25rem;
    width: auto;
  }
}
@media (max-width: 480px) {
  .hero-image {
    height: 20rem;
    width: auto;
  }
}

.hi_icon {
  display: inline-block;
  animation: tilt-n-move-shaking 1s infinite ease-in-out;
}
@media (prefers-reduced-motion: reduce) {
  .hi_icon {
    animation: none;
  }
}
@keyframes tilt-n-move-shaking {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(5px, 5px) rotate(5deg);
  }
  50% {
    transform: translate(0, 0) rotate(0deg);
  }
  75% {
    transform: translate(-5px, 5px) rotate(-5deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

.special-i {
  display: inline-block;
  position: relative;
}
.special-i::after {
  content: "";
  height: 15px;
  width: 15px;
  background-color: #e11d48;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, 50%);
}
@media (max-width: 768px) {
  .special-i::after {
    transform: translate(-50%, 20%);
  }
}
@media (max-width: 480px) {
  .special-i::after {
    height: 10px;
    width: 10px;
    transform: translate(-50%, 20%);
  }
}

.about {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  flex-wrap: no-wrap;
  background-color: #f0f4f8;
}
.about p {
  padding: 0.5rem 0;
}

.about-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  gap: 1rem;
}
@media (max-width: 768px) {
  .about-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0;
    flex-wrap: no-wrap;
  }
}

.skills {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  flex-wrap: no-wrap;
  background-color: #ecf3fb;
}

.skills-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: wrap;
}

.skill-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  border-radius: 50%;
  height: 8rem;
  width: 8rem;
  position: relative;
  top: 0;
  cursor: pointer;
  margin: 0.5rem 1rem;
  font-family: "Cutive Mono", monospace;
  padding: 2rem;
  display: inline-block;
  background-color: #FFFFFF;
}
.skill-item:hover, .skill-item:focus {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.skill-item:hover, .skill-item:focus {
  top: -0.5rem;
}
.skill-item:hover span, .skill-item:focus span {
  visibility: visible;
  text-decoration: none;
}
@media (max-width: 480px) {
  .skill-item {
    height: 6rem;
    width: 6rem;
  }
}
.skill-item span {
  visibility: hidden;
  background-color: #e11d48;
  color: #FFFFFF;
  font-family: "Syne Mono", monospace;
  text-align: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  height: auto;
  width: max-content;
  transition: visibility 0.3s ease;
}
.skill-item:hover, .skill-item:focus {
  top: -0.5rem;
}
.skill-item:hover span .skill-item:focus span {
  visibility: visible;
  text-decoration: none;
}

.experience {
  background-color: #fdf2f8;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  flex-wrap: no-wrap;
}
.experience ul {
  list-style-type: circle;
  padding: 1rem;
  font-size: 1.2rem;
}

.timeline {
  position: relative;
  margin: 0 auto;
  height: auto;
  width: 100%;
}
.timeline::after {
  content: "";
  position: absolute;
  height: auto;
  width: 6px;
  background-color: #FFFFFF;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}
@media (max-width: 768px) {
  .timeline::after {
    left: 31px;
  }
}
@media (max-width: 480px) {
  .timeline::after {
    left: 31px;
  }
}

.timeline-item {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  height: auto;
  width: 50%;
}
@media (max-width: 768px) {
  .timeline-item {
    height: auto;
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
}
@media (max-width: 480px) {
  .timeline-item {
    height: auto;
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
}
@media (max-width: 768px) {
  .timeline-item::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }
}
@media (max-width: 480px) {
  .timeline-item::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }
}
.timeline-item::after {
  content: "";
  position: absolute;
  height: 25px;
  width: 25px;
  right: -17px;
  background-color: #FFFFFF;
  border: 4px solid #e11d48;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

.timeline-content {
  padding: 20px 30px;
  background-color: #FFFFFF;
  position: relative;
  border-radius: 6px;
}
@media (max-width: 768px) {
  .timeline-content {
    padding: 20px;
  }
}
@media (max-width: 480px) {
  .timeline-content {
    padding: 15px;
  }
}

.left {
  left: 0%;
}
.left::before {
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  top: 22px;
  z-index: 1;
  right: 30px;
  border: medium solid #FFFFFF;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #FFFFFF;
}
@media (max-width: 768px) {
  .left::after {
    left: 15px;
  }
}
@media (max-width: 480px) {
  .left::after {
    left: 15px;
  }
}

.right {
  left: 50%;
}
@media (max-width: 768px) {
  .right {
    left: 0%;
  }
}
@media (max-width: 480px) {
  .right {
    left: 0%;
  }
}
.right::before {
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  top: 22px;
  z-index: 1;
  left: 30px;
  border: medium solid #FFFFFF;
  border-width: 10px 10px 10px 0;
  border-color: transparent #FFFFFF transparent transparent;
}
.right::after {
  left: -16px;
}
@media (max-width: 768px) {
  .right::after {
    left: 15px;
  }
}
@media (max-width: 480px) {
  .right::after {
    left: 15px;
  }
}

.education {
  background-color: #faf8f0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0;
  flex-wrap: no-wrap;
}

.education-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  gap: 1rem;
}
@media (max-width: 768px) {
  .education-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0;
    flex-wrap: no-wrap;
  }
}
.education-container p {
  font-size: 1.2rem;
}

.education-img {
  height: auto;
  width: 25rem;
  object-fit: fill;
}

@media (max-width: 768px) {
  .education-details {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0;
    flex-wrap: no-wrap;
  }
}

.projects {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  flex-wrap: no-wrap;
  background-color: #f3f8f6;
}
.projects .project-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 2rem;
  flex-wrap: wrap;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  border-radius: 0.5rem;
  height: auto;
  width: 30rem;
  background-color: #FFFFFF;
}
.card:hover, .card:focus {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
@media (max-width: 480px) {
  .card {
    height: auto;
    width: 20rem;
  }
}
.card .card-img {
  height: 20rem;
  width: auto;
  object-fit: cover;
}
.card .card-content {
  padding: 1rem;
  text-align: center;
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 1rem;
  flex-wrap: no-wrap;
}

.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 1rem;
  flex-wrap: no-wrap;
  margin-top: 1rem;
}
@media (max-width: 480px) {
  .btn-container {
    flex-direction: column;
  }
}
.btn-container a {
  background-color: #e11d48;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  transition: all 0.5s ease;
  text-wrap: nowrap;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 0.5rem;
  flex-wrap: no-wrap;
  height: 3rem;
  width: auto;
}
.btn-container a:hover, .btn-container a:focus {
  background-color: #f5dade;
  color: #e11d48;
}
.btn-container a:hover, .btn-container a:focus {
  background-color: #f5dade;
  color: #e11d48;
}
.btn-container a .btn-icon-g {
  fill: #FFFFFF;
}
.btn-container a .btn-icon-svg {
  stroke: #FFFFFF;
}
.btn-container a:hover .btn-icon-g, .btn-container a:focus .btn-icon-g {
  fill: #e11d48;
}
.btn-container a:hover .btn-icon-svg, .btn-container a:focus .btn-icon-svg {
  stroke: #e11d48;
}

.contact {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
  flex-wrap: no-wrap;
  background-color: #fff7ed;
}
.contact p {
  margin: 0.5rem 0;
  font-size: 1.1rem;
}

.contact-links-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: no-wrap;
  padding: 0 1rem;
}
.contact-links-container a {
  height: 5rem;
  width: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: no-wrap;
}
.contact-links-container a:hover .btn-icon-g, .contact-links-container a:focus .btn-icon-g {
  fill: #e11d48;
}

.contact-detail {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 1rem;
  flex-wrap: no-wrap;
  font-size: 1.2rem;
  margin: 0.5rem 0;
}
.contact-detail a {
  color: #e11d48;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}
.contact-detail a:hover, .contact-detail a:focus {
  color: #1e3a8a;
}

.contact-label {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0.5rem;
  flex-wrap: no-wrap;
}
@media (max-width: 480px) {
  .contact-label {
    display: none;
  }
}

.contact-icon {
  height: 2rem;
  width: 2rem;
  display: inline-block;
}

footer {
  text-align: center;
  padding: 1rem;
  background-color: #1e3a8a;
  color: #FFFFFF;
}

.footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 1rem;
  flex-wrap: no-wrap;
}
@media (max-width: 480px) {
  .footer-content {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .divider {
    display: none;
  }
}

.heart {
  text-decoration: none;
  display: inline-block;
  animation: jump-shaking 0.83s infinite;
}
@media (prefers-reduced-motion: reduce) {
  .heart {
    animation: none;
  }
}
@keyframes jump-shaking {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateY(-9px);
  }
  35% {
    transform: translateY(-9px) rotate(17deg);
  }
  55% {
    transform: translateY(-9px) rotate(-17deg);
  }
  65% {
    transform: translateY(-9px) rotate(17deg);
  }
  75% {
    transform: translateY(-9px) rotate(-17deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}